import React, { useState, useRef } from 'react';

export default (initialMinValue = 5, initialSecValue = 0, initialEndValue = 0) => {
  const [min, setMin] = useState(initialMinValue);
  const [sec, setSec] = useState(initialSecValue);
  //  state (   .)
  const [running, setRunning] = useState(false);
  //  stop/start  state
  const time = useRef(300);
  const timerRef = useRef();
  // ref  api 

  const startTimeout = () => {
    console.log('', time);
    !running && setRunning(true);
    //      Flag Stop     

    if (running === true) {
      //  start 
      return null;
    }

    timerRef.current = setInterval(() => {
      setMin(parseInt(time.current / 60));
      setSec(time.current % 60);
      time.current -= 1;
      if (time.current <= -1) {
        stopTimeout();
      }
    }, 1000);
  };

  const stopTimeout = () => {
    clearInterval(timerRef.current);
    setRunning(false);
  };
  // 

  const clearTimer = () => {
    stopTimeout();
    setMin(0);
    setSec(0);
  };
  //  

  return { startTimeout, stopTimeout, time, clearTimer, running, min, sec };
};
