import React, { useCallback } from 'react';
import io from 'socket.io-client';
import { useDispatch, useSelector } from 'react-redux';
import { receiveChat } from '../../redux/actions/chat';
const ChatTestPage = () => {
  const socket = io.connect('http://localhost:3005');

  const chatData = useSelector((state) => state.chat);
  //store state      function
  {
    console.log('?', chatData);
  }
  const dispatch = useDispatch();
  //redux store  action  dispatch  hook

  const socketSubscribe = useCallback(() => {
    socket.on(' ', ({ data }) => {
      console.log('', data);
      dispatch(receiveChat(data));
    });

    console.log('', dispatch(receiveChat()));
  });

  return (
    <div>
      <button onClick={socketSubscribe}></button>
    </div>
  );
};

export default ChatTestPage;
